@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";

.banner {
    height: 950px;
    background-image: url('../../assets/images/bg-image-28.jpg');
    background-size: cover;
    background-position: 60%;
    background-repeat: no-repeat;
    position: relative;

    @media (max-width: 700px) {
        height: auto;
    }

    .bannerContainer {
        @include container();
        @include flex(row, center, center, nowrap);


        .greeting {
            @include text($fontFamily_1, 55px, 1.2em, 700);
            width: 100%;
            margin: 0 0 30px;
            position: relative;
            padding-right: 15px;
            padding-left: 150px;

            @media (max-width: 1300px) {
                padding: 200px 0;
                font-size: 35px;
                letter-spacing: 3px;

            }
            @media (max-width: 830px) {
                padding: 100px 0;
                font-size: 25px;
                letter-spacing: 1px;
            }

            span {
                color: $accentColor;
                font-size: 70px;
            }
        }
    }

}



















